/* CUSTOM CARD ELEMENTS */
.stdCard {
   display: flex;
   flex-direction: column;

   border-radius: 6px;
   box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.1), 0 4px 8px rgba(16, 22, 26, 0.2),
      0 18px 46px 6px rgba(16, 22, 26, 0.2);
   background: #f5f5f5;
   padding-bottom: 20px;
   pointer-events: all;
   -webkit-user-select: text;
   -moz-user-select: text;
   -ms-user-select: text;
   user-select: text;
}
.stdCard:focus {
   outline: 0;
}
.stdCard.bp3-dark,
.bp3-dark .stdCard {
   box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 4px 8px rgba(16, 22, 26, 0.4),
      0 18px 46px 6px rgba(16, 22, 26, 0.4);
   background: #383838;
   color: #f5f8fa;
}

.stdCard-header {
   display: flex;
   flex: 0 0 auto;
   align-items: center;
   border-radius: 6px 6px 0 0;
   box-shadow: 0 1px 0 rgba(16, 22, 26, 0.15);
   background: #ffffff;
   min-height: 40px;
   padding-right: 20px;
   padding-left: 20px;
}
.stdCard-header .bp3-icon-large,
.stdCard-header .bp3-icon {
   flex: 0 0 auto;
   margin-right: 10px;
   color: #5c7080;
}
.stdCard-header .bp3-heading {
   overflow: hidden;
   text-overflow: ellipsis;
   white-space: nowrap;
   word-wrap: normal;
   flex: 1 1 auto;
   margin: 0;
   line-height: inherit;
}
.stdCard-header .bp3-heading:last-child {
   margin-right: 20px;
}
.bp3-dark .stdCard-header {
   box-shadow: 0 1px 0 rgba(16, 22, 26, 0.4);
   background: #434343;
}
.bp3-dark .stdCard-header .bp3-icon-large,
.bp3-dark .stdCard-header .bp3-icon {
   color: #a7b6c2;
}

.stdCard-body {
   flex: 1 1 auto;
   margin: 20px;
   padding: 20px;
   line-height: 18px;
}

.stdCard-footer {
   flex: 0 0 auto;
   margin: 0 20px;
}

.stdCard-footer-actions {
   display: flex;
   justify-content: flex-end;
}
.stdCard-footer-actions .bp3-button {
   margin-left: 10px;
}

