.bp3-submenu > .bp3-popover-wrapper {
   display: block;
}

.bp3-submenu .bp3-popover-target {
   display: block;
}

.bp3-submenu.bp3-popover {
   box-shadow: none;
   padding: 0 5px;
}
.bp3-submenu.bp3-popover > .bp3-popover-content {
   box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.1), 0 2px 4px rgba(16, 22, 26, 0.2),
      0 8px 24px rgba(16, 22, 26, 0.2);
}
.bp3-dark .bp3-submenu.bp3-popover,
.bp3-submenu.bp3-popover.bp3-dark {
   box-shadow: none;
}
.bp3-dark .bp3-submenu.bp3-popover > .bp3-popover-content,
.bp3-submenu.bp3-popover.bp3-dark > .bp3-popover-content {
   box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 2px 4px rgba(16, 22, 26, 0.4),
      0 8px 24px rgba(16, 22, 26, 0.4);
}

/*
      @override
        -- border-radius: 3px -> 6px
*/
.bp3-menu {
   margin: 0;
   border-radius: 6px;
   background: #ffffff;
   min-width: 180px;
   padding: 5px;
   list-style: none;
   text-align: left;
   color: #1e1e1e;
}

.bp3-menu-divider {
   display: block;
   margin: 5px;
   border-top: 1px solid rgba(16, 22, 26, 0.15);
}
.bp3-dark .bp3-menu-divider {
   border-top-color: rgba(255, 255, 255, 0.15);
}

.bp3-menu-item {
   display: flex;
   flex-direction: row;
   align-items: flex-start;
   border-radius: 2px;
   padding: 5px 7px;
   text-decoration: none;
   line-height: 20px;
   color: inherit;
